@font-face {
  font-family: "Trebuchet MS";
  src: url("../fonts/trebuchet-ms.woff2") format("woff2"), url("../fonts/trebuchet-ms.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Exo2.0";
  src: url("../fonts/exo20-thin.woff2") format("woff2"), url("../../fonts/exo20-thin.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 80px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 14px;
  }
}

.product {
  position: relative;
  color: #000000;
  background-image: url("../img/background/pattern.png");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .product {
    background-image: url("../img/background/pattern@2x.png");
    background-size: contain;
  }
}
.product::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.00005) 49.88%, rgba(0, 0, 0, 0.5) 100%);
}

.product__container {
  padding-top: 40px;
  padding-bottom: 67px;
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (max-width: 1023px) {
  .product__container {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .product__container {
    padding-top: 20px;
    padding-bottom: 30px;
  }
}

.product__container h1 {
  margin: 0 0 21px;
  font-family: "Exo2.0", "Arial", sans-serif;
  font-weight: 100;
  font-size: 36px;
  line-height: 42px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 1px #000000;
}

.product__list {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .product__list {
    justify-content: center;
    flex-wrap: wrap;
  }
}

.product__item {
  max-width: 320px;
  width: 100%;
}
@media (max-width: 1023px) {
  .product__item {
    margin: 3%;
  }
}

.product__item:focus-visible {
  outline: none;
}

.product__item:focus-visible:not(.disabled):not(.selected) .product__card {
  border: 4px solid #2ea8e6;
  outline: none;
}

.product__item:focus-visible:not(.disabled):not(.selected) .product__card::before {
  background-color: #2ea8e6;
}

.product__item:focus-visible:not(.disabled):not(.selected) .product__number {
  background-color: #2ea8e6;
}

.product__item:hover:not(.disabled):not(.selected) .product__card::before {
  background-color: #2ea8e6;
}

.product__item:hover:not(.disabled):not(.selected) .product__card {
  border: 4px solid #2ea8e6;
}

.product__item:hover:not(.disabled):not(.selected) .product__number {
  background-color: #2ea8e6;
}

.product__card {
  padding: 19px 48px 100%;
  margin: 0 0 15px;
  max-height: 480px;
  position: relative;
  background-color: #f2f2f2;
  border: 4px solid #1698d9;
  border-radius: 12px;
  clip-path: polygon(14% 0, 100% 0, 100% 100%, 0 100%, 0% 9%);
  background-image: url("../img/background/cat.png");
  background-repeat: no-repeat;
  background-position: -4px 205px;
  cursor: pointer;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .product__card {
    background-image: url("../img/background/cat@2x.png");
    background-size: cover;
  }
}
.product__card::before {
  position: absolute;
  content: "";
  top: -15px;
  left: 15px;
  width: 6px;
  height: 70px;
  background: #1698d9;
  display: block;
  transform: rotate(45deg);
}

.product__card h2 {
  margin: 0 0 5px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #666666;
}

.product__card h3 {
  margin: 0;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: inherit;
}

.product__item .product__card p {
  text-align: start;
}

.product__card p:first-of-type {
  margin: 0 0 15px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: inherit;
}

.product__card p:last-of-type {
  margin: 0;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #666666;
}

.product__number {
  padding: 21px 0 0 0;
  position: absolute;
  bottom: 12px;
  right: 12px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  color: #ffffff;
  text-align: center;
  background-color: #1698d9;
}

.product__number b {
  margin: 0;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 42px;
  line-height: 22px;
  color: #ffffff;
}

.product__card .product__number > p {
  margin: 0;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 21px;
  line-height: 22px;
  text-align: center;
  color: #ffffff;
}

.product__item p {
  margin: 0;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #ffffff;
}

.product__item span {
  cursor: pointer;
  color: #1698d9;
  text-decoration: underline;
  text-decoration-style: dashed;
}

.selected .product__card {
  border-color: #d91667;
}

.selected .product__card::before {
  background-color: #d91667;
}

.selected .product__number {
  background-color: #d91667;
}

.selected:hover .product__card {
  border-color: #e52e7a;
}

.selected:hover .product__card::before {
  background-color: #e52e7a;
}

.selected:hover .product__number {
  background-color: #e52e7a;
}

.disabled .product__card {
  border: 4px solid #b3b3b3;
}

.disabled .product__card > p,
.disabled .product__card > h2,
.disabled .product__card > h3 {
  color: #b3b3b3;
  opacity: 0.5;
}

.disabled .product__card::after {
  position: absolute;
  content: "";
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #f2f2f2;
  z-index: -1;
}

.disabled .product__card::before {
  background-color: #b3b3b3;
}

.disabled .product__number {
  background-color: #b3b3b3;
}

.disabled p {
  color: #FFFF66;
}

